@tailwind base;
@tailwind components;
@tailwind utilities;

.content-div {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 250px;
    width: auto;
}

.content-div:hover {
    background-image: linear-gradient(to right,
            rgba(112, 157, 255, 0.8),
            hsla(242, 74%, 61%, 0.8))!important; }